<template>
  <div class="add-device">
    <mu-appbar :title='deviceInfo.deviceAlias' class='header'>
      <mu-icon-button slot='left' icon='keyboard_arrow_left' style='color: #fff' @click="this.history.back()"/>
    </mu-appbar>
    <div class="bottom">
      <div class="choose-attr">
        <el-select v-model="attribute" placeholder="" @change="selectAttributeChange">
          <el-option v-for="(value,key) in deviceDate.state.reported" :key="key" :label="key" :value="key">
          </el-option>
        </el-select>
      </div>
      <div id="echart" ref="mychart" style="width: 100%;height: 400px;">
      </div>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts';
  let xData = [];//
  let yData = [];//
  export default{
    data(){
      return {
        /*store*/
        deviceInfo: {
          deviceName: '',
          productKey: '',
          deviceAlias: '',
        },
        /**/
        interval: 0,
        attribute: ''
      }
    },
    created() {
      /**/
      this.deviceInfo = this.$store.state.devices.deviceinfo;
      //,,,,
      for (let key in this.deviceDate.state.reported) {
        this.attribute = key;
        break;
      }
    },
    mounted () {
      //DOM,,DOM,
      let myChart = this.setCharts();
      //
      this.interval = setInterval(() => {
        let postObj = {};
        postObj.productKey = this.deviceInfo.productKey;
        postObj.deviceName = this.deviceInfo.deviceName;
        this.$store.dispatch('getDeviceDate', postObj).then(() => {
          //
//        xData.push(new Date(parseInt(this.deviceDate.timestamp) * 1000).toLocaleString().substr(0, 24));
          xData.push(new Date().toLocaleTimeString());
          yData.push(this.deviceDate.state.reported[this.attribute]);
          myChart.setOption({
            xAxis: {
              data: xData
            },
            series: [{
              data: yData
            }]
          });
        }).catch(err => {
          console.log(err);
        });
      }, 2000);
    },
    beforeDestroy() {
      xData = [];
      yData = [];
      console.log('' + this.interval);
      clearInterval(this.interval);
    },
    computed: {
      deviceDate() {
        return this.$store.state.devices.deviceDate;
      },
      //,
      optionConfig() {
        return {
          title: {text: ''},
          xAxis: {
            data: xData
          },
          yAxis: {},
          series: [{
            type: 'line',
            smooth: true,
//            symbol: 'none',
            stack: 'a',
            areaStyle: {
              normal: {}
            },
            data: yData
          }]
        }
      }
    },
    methods: {
      /**/
      setCharts() {
        let dom = this.$refs.mychart;
        let myChart = echarts.init(dom);
        myChart.showLoading();
        myChart.setOption(this.optionConfig);
        myChart.hideLoading();
        return myChart;
      },
      /**/
      selectAttributeChange(){
        xData = [];
        yData = [];
      },
      /*store*/
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  .select {
    text-align: left;
  }

  .add-device {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    background-color: #fff;
    .page-part {
      padding-top: 62px;
    }
    .bottom {
      margin-top: 62px;
      .choose-attr {
        margin-top: 72px;
      }
    }
  }
</style>




